import Slide from './Slide';
import SlideForm from './SlideForm';
import ImportSave from './ImportSave';
export default {
  components: {
    'v-slide': Slide,
    'v-slide-form': SlideForm,
    'c-import-save': ImportSave
  },
  data: function data() {
    return {
      timer: window.localStorage.getItem('timer'),
      slides: JSON.parse(window.localStorage.getItem('slides') || '[]') || [],
      configUrl: window.localStorage.getItem('configUrl'),
      currentSlide: window.localStorage.getItem('currentSlide'),
      isPlaying: JSON.parse(window.localStorage.getItem('isPlaying')),
      isEditing: false,
      isNew: false,
      editingSlide: {},
      editingSlideIndex: null
    };
  },
  mounted: function mounted() {},
  methods: {
    save: function save() {
      if (this.timer) window.localStorage.setItem('timer', this.timer);
      if (this.configUrl) window.localStorage.setItem('configUrl', this.configUrl);
      if (this.currentSlide) window.localStorage.setItem('currentSlide', this.currentSlide);
      if (this.isPlaying) window.localStorage.setItem('isPlaying', this.isPlaying);
      console.log('💾');
    },
    loadSlides: function loadSlides() {
      this.slides = JSON.parse(window.localStorage.getItem('slides') || '[]') || [];
    },
    addNewSlide: function addNewSlide() {
      this.editingSlide = {
        props: {}
      };
      this.isEditing = true;
      this.isNew = true;
    },
    activeEdition: function activeEdition(index) {
      this.editingSlideIndex = index;
      this.editingSlide = Object.assign({}, this.slides[index]);
      this.isEditing = true;
    },
    cancel: function cancel() {
      this.isEditing = false;
      this.isNew = false;
    },
    deleteSlide: function deleteSlide() {
      this.slides.splice(this.editingSlideIndex, 1);
      this.isEditing = false;
      this.isNew = false;
    },
    validate: function validate(slide) {
      if (this.isNew) {
        this.slides.push(slide);
      } else {
        this.slides[this.editingSlideIndex] = Object.assign({}, slide);
      }

      this.isEditing = false;
      this.isNew = false;
      window.localStorage.setItem('slides', JSON.stringify(this.slides));
    }
  }
};