export default {
  data: function data() {
    return {
      slideUrl: null,
      json: null,
      saveName: null,
      slides: JSON.parse(window.localStorage.getItem('slides') || '[]') || []
    };
  },
  mounted: function mounted() {
    document.getElementById('file').addEventListener('change', this.onChange);
  },
  methods: {
    onChange: function onChange(event) {
      var reader = new FileReader();
      reader.onload = this.onReaderLoad;

      if (event.target.files[0]) {
        reader.readAsText(event.target.files[0]);
      }
    },
    onReaderLoad: function onReaderLoad(event) {
      this.json = JSON.parse(event.target.result);
    },
    importFromFile: function importFromFile() {
      if (this.json) {
        localStorage.setItem('slides', JSON.stringify(this.json));
        console.log('💾 save json');
        this.$emit('onSlidesImport');
        this.json = null;
      }
    },
    importFromUrl: function importFromUrl() {
      var _this = this;

      if (this.slideUrl) {
        fetch(this.slideUrl).then(function (resp) {
          return resp.json();
        }).then(function (data) {
          _this.slides = data;
          localStorage.setItem('slides', JSON.stringify(data));

          _this.$emit('onSlidesImport');

          _this.slideUrl = null;
        });
      }
    },
    exportConf: function exportConf() {
      this.slides = JSON.parse(window.localStorage.getItem('slides') || '[]') || [];

      if (this.saveName && this.slides.length) {
        this.downloadObjectAsJson(this.slides, this.saveName);
      }
    },
    downloadObjectAsJson: function downloadObjectAsJson(exportObj, exportName) {
      var dataStr = "data:text/json;charset=utf-8," + encodeURIComponent(JSON.stringify(exportObj));
      var downloadAnchorNode = document.createElement('a');
      downloadAnchorNode.setAttribute("href", dataStr);
      downloadAnchorNode.setAttribute("download", exportName + ".json");
      document.body.appendChild(downloadAnchorNode); // required for firefox

      downloadAnchorNode.click();
      downloadAnchorNode.remove();
    }
  }
};